<template>
 
  <h2 hidden>{{state.circleid = 6 }}</h2>
  <section class="tutoCircleRead tutoBox" ref="tutoCircleRead">
    <h4>여기에 그룹에 대한 상세 설명이 있어요!</h4>
    
      <button class="btn btn-success tutoBtn" style="background:rgba(6, 90, 92, 0.5);" @click.prevent="goStep1of4">네 그렇군요  </button>
      
  </section>
  <form class="circleInfo circleInfoIfNotFounder container" ref="circleInfo2" action="">
    <div class="row">
      <div class="mb-3 col">
        <label for="inputName" class="form-label">그룹 이름</label>
        <input type="text" class="form-control" id="inputName" ref="circlename" v-model="state.name" disabled>
      </div>
      <div class="mb-3 col">
        <label for="inputNumber" class="form-label"><img alt="Vue logo" src="../assets/icones/usericon.svg"> 최대 인원</label>
        <div><input type="number" class="form-control" id="inputNumber" ref="maxNumber" v-model="state.maxNum" disabled>
        </div>
      </div>
      <div class="mb-3 col">
        <label for="inputPassword" class="form-label">비밀번호</label>
        <input type="password" class="form-control" id="inputPassword" v-model="state.pass" disabled>
      </div>
    </div>
    <div class="form-floating mb-3 ">
      <textarea class="form-control" id="floatingTextarea2" v-model="state.content" disabled
        style="height: 80px"></textarea>
      <label for="floatingTextarea2">그룹 상세 내용</label>
    </div>
    <div class="item buttons" ref="buttons" style="text-align:right">
      <button class="btn btn-dark" @click.prevent="state.showModal=true">나가기</button>
    </div>
  </form>
  
  <section class="tutoNewsFeed tutoBox" ref="tutoNewsFeed" style="display:none;">
    <h4 ref="newsFeedDesc">와! 많은 회원들이 목표를 향해 전진하고 있어요</h4>
    <button  class="btn btn-success tutoBtn" id="goStep2of4" style="background:rgba(6, 90, 92, 0.5);" @click.prevent="goStep2of4">네!</button>
  </section>
  <section class="tutoNewsFeed tutoBox " ref="tutoFAQ" style="display:none;">
    <h4 ref="faqDesc">이 곳에 질문과 답변을 자유롭게 올릴 수 있어요.</h4>
      <button class="btn btn-success tutoBtn" id="goStep3of4" @click.prevent="goStep3of4">멋지네요!</button>
    </section>

    <div class="row">
      <div ref="newsFeed" class="col">
        <NewsFeed :circleid="8"></NewsFeed>
      </div>
      <div ref="fAQ"  class="col">
        <img src="../assets/images/pictures/QnA.png" alt="team" >

      </div>

      <section class="tutoNewsFeed tutoBox" ref="tutoUserList" style="display:none;">
          <h4>이 그룹의 회원들이에요. 한 명의 회원을 클릭해 그의 여정을 따라가 보세요.</h4>
        </section>
    </div>
      <!-- CircleUserList doesnt work yet -->
      <RouterLink to="/tutoprivate" id="goStep4of4"> 
        <div ref="circleUserList">
        <h2 >회원목록</h2>
        <div class="image-container">
          <el-tooltip v-for="(list, i) in state.img" :key="(list, i)" effect="dark" v-bind:content="state.lists[i].nickname"
            placement="top">
            <img v-if="state.lists[i].path == null" src="../assets/images/pictures/profile_icon.png" @click="showUserList(i)">
            <img v-else="!(state.lists[i].path == null)" v-bind:src="state.img[i]" alt="프로필사진" @click="showUserList(i)">
          </el-tooltip>
        </div>
      </div>
      </RouterLink>
  <Teleport to="body">
    <modal :show="state.showModal" @close="state.showModal=false">
      <template #header>
        <h2>나가기</h2>
      </template>
      <template #body>
        <div>
          <h5>가입 후 사용 가능한 기능입니다</h5>
        
        </div>
      </template>
      <template #footer>
        <el-button type="primary" plain @click="state.showModal=false" >확인</el-button>
      </template>
    </modal>
  </Teleport>
  <a id="here">you are here</a>
</template>
<script>
import { reactive, ref } from '@vue/reactivity'
import axios from 'axios'
import { onMounted } from 'vue'
import { useRouter } from 'vue-router';
import Modal from './teleport/Modal.vue'
import NewsFeed from './TheNewsFeed.vue'
import QuestionByCircle from './QuestionByCircle.vue'
import CircleUserListForPublic from './CircleUserListForPublic.vue'
export default {
  components: { Modal, NewsFeed, QuestionByCircle, CircleUserListForPublic },
  setup() {
    onMounted(() => { getCircle() })
    const router = useRouter();
    const state = reactive({
      roleSet: '',
      name: '',
      content: '',
      maxNum: '',
      pass: '',
      circleid: '8',
      img: new Array(),
      lists: '',
      isAmember:true,
      email: sessionStorage.getItem('email'),
      token: sessionStorage.getItem("TOKEN"),
      showModal: false,
    })
    const circlename = ref(null)
    const frm = ref(null)
    let notice = ref(null)
    let maxNotice = ref(null)
    let maxNumber = ref(null)
    
    //for the tuto
    let tutoCircleRead = ref(null);
    let circleInfo2 = ref(null);

    let tutoNewsFeed = ref(null);
    let newsFeedDesc = ref(null);
    let newsFeed = ref(null);
    let faqDesc = ref(null);
    
    let tutoFAQ= ref(null);
    let fAQ = ref(null);
    
    let tutoUserList = ref(null);
    let circleUserList= ref(null);
    const scrollPageTo = (navEl) => {
            console.log(`#${navEl}`);
            let element = document.querySelector(`#${navEl}`);
            console.log(element);
            element.scrollIntoView({behavior: 'smooth'});
        }
    const goStep1of4 = () => {
      circleInfo2.value.style.border = "0px solid #b6f67c";
      tutoCircleRead.value.style.display = "none";
      newsFeed.value.style.border = "30px solid #065a5c";
      tutoNewsFeed.value.style.display = "";
      faqDesc.value.style.display = 'none'
      // scrollPageTo('goStep2of4');
    }
    const goStep2of4 = () => {
      newsFeed.value.style.border = "0px solid #b6f67c";
      tutoNewsFeed.value.style.display = "none";
      fAQ.value.style.border = "30px solid #065a5c";
      tutoFAQ.value.style.display = "";
      faqDesc.value.style.display = ''
      // scrollPageTo('goStep3of4');
      
    }
    const goStep3of4 = () => {
      fAQ.value.style.border = "0px solid #b6f67c";
      tutoFAQ.value.style.display = "none";
      newsFeedDesc.value.style.display = "none"
      circleUserList.value.style.border = "30px solid #065a5c";
      tutoUserList.value.style.display = "";
      faqDesc.value.style.display = ''
      scrollPageTo('goStep4of4');

    }
    const goStep4of4 = () => {
      // go the the next tuto page
      // router.push({ name: "goStep4of4" });
    }
    const getCircle = async () => {
      console.log("myreadCircle launch")
      const url = "http://localhost:9090/api/circleread"

      const headers = {
        "Content-Type": "application/json",
        Authorization: state.token,
        "token": state.token,
      }
      const body = { circleid: '8', email: 'hyelan@ds.org' }
      await axios.post(url, body, { headers }).then(function (res) {
        if (res.status === 200) {
          state.name = res.data.dto.name;
          state.content = res.data.dto.content;
          state.maxNum = res.data.dto.maxNum;
          state.pass = res.data.dto.pass;
          state.roleSet = res.data.dto.roleSet;
          state.lists = res.data.img;
          showImage(state.lists)
        } else {
          alert('fail')
        }
      })
    }
    const showImage = async (file) => {
      const url = `http://localhost:9090/display`
      let str = "";
      console.info("Thumbnail" + file[0].thumbnailURL)
      console.info("Thumbnail" + file[1].thumbnailURL)
      for (let i = 0; i < file.length; i++) {
        str = `${url}?fileName=${file[i].thumbnailURL}`;
        state.img.push(str);
      }
    }
    const showUserList = async (i) => {
      if (JSON.stringify(state.uid) == JSON.stringify(state.lists[i].uid)) {
        router.push({ name: "privateProfile" });
      } else if (!(JSON.stringify(state.uid) == JSON.stringify(state.lists[i].uid))) {
        router.push({ name: 'publicProfile', params: { id: state.lists[i].uid } });
      }
    }
    return { circlename, circleInfo2, newsFeed,
      scrollPageTo,
      tutoCircleRead,tutoNewsFeed, tutoFAQ,fAQ,tutoUserList,circleUserList,
      goStep1of4,goStep2of4, goStep3of4,goStep4of4,
      maxNotice, maxNumber, frm, notice, getCircle, state,  
       newsFeedDesc,faqDesc,showUserList,showImage }
  }
}
</script>
<style scoped>
.tutoBox {
  padding: 1em;
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
	background-size: 200% 200%;
	animation: gradient 10s ease infinite;
  border-radius: 1em;
  /* padding: 3em; */
}
@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

.circleInfo {
  border: 30px solid  rgba(6, 90, 92);
  border-radius: 30px;
}

input:focus {
  box-shadow: 0px 6px 10px rgba(6, 90, 92, 0.25);
  transition: all 0.2s;
  border: 1px solid rgba(6, 90, 92, 0.5);
}

textarea:focus {
  box-shadow: 0px 6px 10px rgba(6, 90, 92, 0.25);
  transition: all 0.2s;
  border: 1px solid rgba(6, 90, 92, 0.5);
}

.btn {
  margin: 0.7em;
}

.image-people {
  width: 1.4em;
}

.image-item {
  display: inline;
}

.image-container img {
  display: inline;
  cursor: pointer;
}

.image-container {
  display: grid;
  grid-template-columns: 100px 100px 100px 100px 100px 100px 100px 100px 100px 100px 100px 100px;
  background-color: white;
  column-gap: 0px;
}
/* Designing for scroll-bar */
::-webkit-scrollbar {
      width: 6px;
  }

  /* Track */
  ::-webkit-scrollbar-track {
      background: gainsboro;
      border-radius: 5px;
  }

  /* Handle */
  ::-webkit-scrollbar-thumb {
      background: black;
      border-radius: 5px;
  }

  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
      background: #555;
  }
</style>
    